@import '../../variables.scss';

$buttonBorderRadius: 22px;

.button-simple {
  padding: 10px 20px;
  border: none;
  outline: none;

  background: transparent;
  color: $mainColor;

  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;

  cursor: pointer;

  &:hover {
    color: $fontColorLight50;
  }

  &:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}

.button-solid {
  border: none;
  padding: 10px 20px;
  border-radius: $buttonBorderRadius;
  outline: none;

  background: $mainColorGradient;
  color: white;

  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: inherit;

  cursor: pointer;

  &:hover {
    opacity: 0.5;
  }

  &_slim {
    padding: 8px 21px 8px 19px;
  }

  &:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}

.button-bordered {
  display: flex;
  align-items: center;
  position: relative;

  border: none;
  padding: 2px;
  border-radius: $buttonBorderRadius;
  outline: none;

  font-size: 12px;
  color: $mainColor;
  text-transform: uppercase;
  font-weight: bold;
  line-height: inherit;

  cursor: pointer;

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    border-radius: $buttonBorderRadius + 2;

    background: $mainColorGradient;
  }

  &__inner {
    display: block;
    z-index: 1;

    border: 2px solid rgba($color: #fff, $alpha: 0.5);
    padding: 6px 14px;
    border-radius: inherit;

    background: white;
  }

  &_slim > &__inner {
    padding: 4px 16px 4px;
  }

  &:hover > &__background {
    opacity: 0.5;
  }

  &:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}

.button-solid-with-icon {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;

  border: none;
  padding: 15px 20px + 18px; // 18px - width icon
  border-radius: $buttonBorderRadius;
  outline: none;

  background: $mainColorGradient;
  color: white;

  font-size: 12px;
  text-transform: uppercase;
  line-height: 14px;

  cursor: pointer;

  &__content {
    width: 100%;
  }

  &__icon {
    position: absolute;
    right: 12px;
    bottom: 10px;
  }

  &__icon > svg > path {
    fill: white;
  }

  &:hover {
    opacity: 0.5;
  }

  &:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}
