@import '../../variables.scss';

.bullet-list {
  display: block;
  width: 100%;

  &__header {
    margin-top: 0;
    margin-bottom: 16px;
    margin-left: 20px;

    color: $fontColor;

    font-family: $headerFont;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 24px;
  }

  &__item {
    display: block;
    position: relative;
    margin-top: 10px;
    padding-left: 20px;

    font-size: 14px;
    line-height: 24px;
  }

  &__item::before {
    content: '';
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    top: 7px;
    left: 0;

    border-radius: 5px;

    background-color: $fontColorLight25;
  }
}
