@import '../../variables.scss';

.comments {
  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 19px;

    color: $fontColor;

    font-family: $headerFont;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 24px;
  }

  &__comments-count {
    color: $fontColorLight50;

    margin-top: 6px;

    text-align: center;
  }

  &__item {
    margin-bottom: 22px;
  }
}
