@import '../../variables.scss';

.comment {
  display: flex;
  flex-direction: row;
  width: 100%;

  &__left-aside {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50px;
    flex-shrink: 0;
  }

  &__avatar {
    display: block;
    width: 50px;
    height: 50px;

    margin-bottom: 11px;

    border-radius: 25px;
    border: 2px solid white;
    box-shadow: 0 10px 20px rgba(31, 32, 65, 0.1);
  }

  &__avatar-image {
    width: 100%;
    height: 100%;
    border-radius: 25px;
  }

  &__main {
    display: block;
    padding-left: 9px;

    color: $fontColorLight75;

    font-size: 14px;
    line-height: 24px;
  }

  &__name {
    display: block;
    margin-top: 1px;
    margin-bottom: 0;

    font-size: 14px;
    line-height: 24px;
    text-transform: capitalize;
    font-weight: bold;
  }

  &__date {
    display: block;
    margin-bottom: 10px;
  }
}
