@import '../../variables.scss';

.room-detail-form {
  width: 100%;
  max-width: 380px;

  padding: 30px;

  color: $fontColorLight75;
  background-color: white;

  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0 10px 20px $fontColorLight5;
  border-radius: 4px;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 11px;
    margin-bottom: 20px;
  }

  &__top-room {
    position: relative;
  }

  &__top-room span {
    margin-right: 4px;

    font-family: $headerFont;
    font-size: 24px;
    line-height: 24px;
    font-weight: bold;
  }

  &__top-room &__top-numero-sign {
    font-family: $mainFont;
    font-size: 14px;
  }

  &__top-room &__top-lux {
    color: $mainColor;

    font-family: $mainFont;
    font-size: 12px;
    text-transform: uppercase;
  }

  &__top-cost {
    margin-top: 4px;

    font-weight: bold;
  }

  &__top-cost-end {
    font-weight: normal;
    font-size: 12px;
    margin-left: 5px;
  }

  &__cost-per-days,
  &__discount,
  &__additional-fee {
    display: flex;
    justify-content: space-between;
  }

  &__cost-per-days {
    margin-top: 20px;
  }

  &__cost-per-days-left > span {
    margin-right: 4px;
  }

  &__discount {
    margin-top: 17px;
  }

  &__discount-left,
  &__additional-fee-left {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__discount-right,
  &__additional-fee-right {
    min-width: 80px;
    flex-shrink: 1;
    text-align: right;
  }

  &__additional-fee {
    margin-top: 13px;
  }

  &__info-cost {
    position: relative;
    top: -2px;
    right: -2px;

    color: $fontColorLight50;

    font-size: 14px;
    font-weight: bold;
  }

  &__date-range {
    margin-bottom: 21px;
  }

  &__result {
    display: flex;
    justify-content: space-between;

    margin-top: 33px;

    color: $fontColor;
  }

  &__result-label,
  &__result-sum {
    text-transform: capitalize;
    font-weight: bold;
    font-size: 19px;
    font-family: $headerFont;
  }

  &__result-sum {
    position: relative;
    right: -3px;
    flex-shrink: 0;
  }

  &__result-filler {
    border-bottom: 1px dotted $fontColorLight25;
    width: 100%;
    position: relative;
    top: -2px;
  }

  &__submit {
    margin-top: 23px;
  }

  &__info-icon {
    min-width: 20px;
  }
}

.info-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 20px;
  height: 20px;

  background-color: white;
  color: $fontColorLight25;

  border: 1px solid $fontColorLight25;
  border-radius: 10px;

  font-size: 12px;

  cursor: pointer;

  &__message {
    display: none;
    position: absolute;
    top: 30px;
    left: -100px;
    z-index: 1;

    padding: 1px 5px;

    background-color: white;
    color: $fontColorLight75;
    border: 1px solid $fontColorLight5;
    border-radius: 4px;

    font-size: 10px;
  }

  &:hover &__message {
    display: block;
  }
}
