@import '../../variables.scss';

.pie-chart {
  &__title {
    margin-bottom: 21px;

    color: $fontColor;

    font-family: $headerFont;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 24px;
  }

  &__content {
    display: flex;
    align-items: flex-end;
  }

  &__legends {
    font-size: 14px;
    line-height: 24px;

    margin-bottom: 12px;
    margin-left: 14px;

    color: $fontColorLight75;
  }

  &__message {
    display: block;
    position: absolute;
    width: 120px;

    margin-top: 41px;

    color: $mainColor;

    font-size: 12px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
  }

  &__voices-count {
    margin-bottom: 5px;

    font-family: $headerFont;
    font-size: 24px;
  }

  &__legend-item-verygood::before {
    background: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
  }

  &__legend-item-good::before {
    background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
  }

  &__legend-item-satisfactorily::before {
    background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  }

  &__legend-item-bad::before {
    background: linear-gradient(180deg, #919191 0%, #3d4975 100%);
  }
}

[class^='pie-chart__legend-item'] {
  position: relative;

  padding-left: 20px;
}

[class^='pie-chart__legend-item']::before {
  display: block;
  position: absolute;
  content: '';
  width: 10px;
  height: 10px;
  left: 5px;
  top: 7px;

  border-radius: 5px;
}

@media (max-width: 650px) {
  .pie-chart {
    &__legends {
      margin-bottom: auto;
      margin-top: 10px;
      margin-left: 0;
    }
  }
}
