@import '../../variables.scss';

.room-detail-page {
  &__photos {
    display: grid;
    grid-template-areas:
      'first first second'
      'first first third';
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    max-width: 1440px;
    max-height: 485px;

    margin-right: auto;
    margin-left: auto;

    overflow: hidden;
  }

  &__photos-first {
    grid-area: first;
  }

  &__photos-second {
    grid-area: second;
  }

  &__photos-third {
    grid-area: third;
  }

  &__photo-img {
    display: block;
    max-width: 100%;
    width: 100%;
    height: 100%;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;

    margin: 0 auto;
    padding-top: 52px;
    padding-left: 18px;
    padding-right: 20px;
  }

  &__content-info {
    flex-grow: 2;
  }

  &__content-form {
    flex-shrink: 0;

    padding-top: 20px;
  }

  &__content-info-top,
  &__content-info-bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__content-room-features {
    margin-left: 2px;
  }

  &__content-room-chart {
    margin-right: auto;
    margin-left: auto;
  }

  &__content-info-feedback {
    padding-top: 12px;
    padding-right: 70px;
  }

  &__content-info-bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    margin-top: 34px;
    margin-bottom: 73px;
  }

  &__content-info-bottom-rules {
    max-width: 262px;

    margin-left: 2px;
  }

  &__content-info-bottom-cancel {
    max-width: 322px;

    margin-left: auto;
    margin-right: auto;
  }

  &__content-info-bottom-cancel-title {
    margin: 0;
    margin-bottom: 16px;

    color: $fontColor;

    font-family: $headerFont;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 24px;
  }

  &__content-info-bottom-cancel-content {
    line-height: 24px;
  }
}

@media (max-width: 1000px) {
  .room-detail-page__content {
    flex-direction: column-reverse;
    align-items: center;
  }

  .room-detail-page__content-form {
    padding-top: 0;
  }

  .room-detail-page__content-info-top {
    justify-content: space-around;

    margin-top: 40px;
    margin-bottom: 40px;
  }

  .room-detail-page__content-room-chart {
    margin: unset;
  }
}

@media (max-width: 650px) {
  .room-detail-page__content-info-top,
  .room-detail-page__content-info-bottom {
    flex-direction: column;
    align-items: center;
  }

  .room-detail-page__content-info-bottom-rules {
    max-width: 100%;

    margin-left: 10px;
    margin-bottom: 40px;
  }

  .room-detail-page__content-info-feedback {
    padding-right: 0;
    padding-top: 0;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .room-detail-page__content-room-chart {
    width: 280px;
  }
}

@media (max-width: 500px) {
  .room-detail-page__photos {
    display: block;
    max-height: none;
  }
}

@media (max-width: 400px) {
  .room-detail-page__content-info-bottom-cancel {
    min-width: 250px;
    margin-left: 30px;
  }
}
