@import '../../variables.scss';

.top-bar {
  position: relative;
  padding-top: 20px;
  padding-bottom: 11px;

  box-shadow: 0 10px 20px $fontColorLight5;

  &__logo-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__center {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: inherit;

    margin: 0 auto;
    padding: 0 20px;

    max-width: 1200px;
  }

  &__menu {
    display: flex;
    flex-direction: row;
    margin-left: auto;

    margin-bottom: 2px;
    margin-right: 8px;

    transition: max-height, margin 0.1s ease-in-out;
  }

  &__menu-item {
    display: block;

    padding-left: 20px;

    color: $fontColorLight50;

    font-size: 14px;

    cursor: pointer;
  }

  &__menu-expanded {
    position: relative;

    padding-right: 24px;
  }

  &__menu-expanded::before {
    display: block;
    position: absolute;
    content: '';
    width: 14px;
    height: 8px;
    right: 3px;
    top: 6px;

    background: url('img/expand_more_down.svg');
    background-repeat: no-repeat;
    opacity: 0.5;
  }

  &__menu-expanded:hover ul.top-bar__submenu {
    display: block;

    z-index: 100;
  }

  &__submenu {
    display: none;
    position: absolute;
    width: 150px;
    left: 5px;

    padding-top: 10px;
    padding-left: 10px;
    box-shadow: 0 10px 20px $fontColorLight5;

    background-color: white;
  }

  &__submenu-item {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__menu-link {
    color: $fontColorLight50;

    text-decoration: none;

    outline: none;
  }

  &__menu-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__menu-item_active {
    font-weight: bold;
  }

  &__user {
    margin-left: 15px;
  }

  &__switch-button {
    display: none;
    position: absolute;
    top: 27px;
    right: 30px;
    width: 24px;
    height: 24px;

    border: none;
    outline: none;

    background-color: transparent;
    background-image: url('img/close.svg');
    background-repeat: no-repeat;

    cursor: pointer;
  }

  &__switch-button:focus {
    outline: auto;
    outline-color: $mainColor;
  }
}

@media (max-width: 970px) {
  .top-bar {
    height: 120px;
    padding-top: 20px;

    &__center {
      flex-wrap: wrap;
    }

    &__user {
      order: 2;
    }

    &__menu {
      order: 3;
      width: 100%;
      margin-left: auto;
      justify-content: space-between;
      height: 60px;
      padding-top: 15px;
    }
  }
}

// TODO: make adaptive for mobile
@media (max-width: 600px) {
  .top-bar {
    height: auto;

    &__menu {
      display: flex;
      flex-direction: column;
      align-items: start;
      height: auto;
      max-height: 500px;

      padding-top: 0;
      margin-top: 10px;
      margin-bottom: 10px;

      overflow: hidden;
    }

    &__center {
      display: block;
    }

    &__menu-item {
      width: auto;
    }

    &__switch-button {
      display: block;
    }

    &__submenu {
      width: auto;
    }

    &__menu-expanded:hover &__submenu {
      display: block;
      position: relative;

      padding-top: 0;

      box-shadow: none;
    }

    &__menu-expanded:hover .top-bar__submenu-item {
      padding-top: 2px;
      padding-bottom: 2px;
    }

    &__user {
      margin-left: 0;
    }
  }

  .top-bar_close .top-bar__menu {
    max-height: 0;
    margin-top: 0;
    margin-bottom: 0;
  }

  .top-bar_close .top-bar__switch-button {
    background-image: url('img/menu.svg');
  }
}
