@import '../../variables.scss';

.like-button {
  display: flex;

  border: 1px solid $fontColorLight25;
  border-radius: 10px;
  padding: 3px 4px;
  outline: none;

  background: white;
  color: $fontColorLight25;

  font-size: 10px;
  line-height: 12px;

  cursor: pointer;

  &__icon {
    display: block;
    width: 12px;
    height: 12px;

    margin-right: 3px;

    background-image: url('img/heart_border.svg');
    background-repeat: no-repeat;
    background-position: 0 2px;
  }

  &__count {
    min-width: 15px;
    text-align: left;
  }

  &_checked {
    border: 1px solid $mainColor;

    color: $mainColor;
  }

  &_checked &__icon {
    background-image: url('img/heart.svg');
  }
}
