@import '../../variables.scss';

.features {
  display: block;
  width: 100%;

  &__title {
    margin-bottom: 0;

    color: $fontColor;

    font-family: $headerFont;
    font-size: 19px;
    line-height: 24px;
    font-weight: bold;
  }

  &__item {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;

    border-bottom: 1px solid $fontColorLight10;

    padding-bottom: 20px;
    padding-top: 20px;
    padding-left: 3px;
  }

  &__item-image {
    position: relative;
    left: -5px;
    top: -2px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    flex-shrink: 0;
  }

  &__item-content {
    padding-left: 5px;

    color: $fontColorLight75;

    font-size: 14px;
    line-height: 24px;
  }

  &__item-title {
    display: block;

    margin: 0;

    font-weight: bold;
    font-size: 14px;
  }
}
