@import '../../variables.scss';

.top-bar-user {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

  &__name {
    padding-left: 30px;

    color: $fontColorLight50;

    border-left: 1px solid $fontColorLight10;

    cursor: pointer;
  }

  &__actions {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-bottom: 2px;
  }

  &__action:focus {
    box-shadow: 0 0 2px 1px $mainColor;
  }

  &__action:nth-child(1) {
    margin-right: 20px;
  }
}

@media (max-width: 600px) {
  .top-bar-user {
    display: block;

    &__actions {
      padding-bottom: 10px;
    }

    &__action {
      display: block;

      margin-top: 5px;
    }
  }
}

@media (max-width: 400px) {
  .top-bar-user {
    &__actions {
      display: block;
    }

    &__action {
      margin-bottom: 5px;
    }
  }
}
